import React, { Component } from "react";

import CardMD1 from "../Shared/CardMD1";
import CardMD2 from "../Shared/CardMD2";
import CardMD3 from "../Shared/CardMD3";

import "../Shared/Cards.css";

class Cards extends Component {
  render() {
    return (
      <React.Fragment>
        <div className="container-fluid d-flex justify-content-center">
          <div className="row">
            <div className="row-eq-height col-md-4">
              <CardMD1></CardMD1>
            </div>
            <div className="row-eq-height col-md-4">
              <CardMD2></CardMD2>
            </div>
            <div className="row-eq-height col-md-4">
              <CardMD3></CardMD3>
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default Cards;
