import React from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import "mdbreact/dist/css/mdb.css";
import "@fortawesome/fontawesome-free/css/all.min.css";
import {
  MDBCard,
  MDBCardBody,
  MDBCardImage,
  MDBCardTitle,
  MDBCardText,
  MDBRow,
  MDBCol,
} from "mdbreact";

import img3 from "../assets/img3.jpg";

// eslint-disable-next-line no-lone-blocks
{
  /*import "@fortawesome/fontawesome-free/css/all.min.css"; */
}

const CardMD3 = () => {
  return (
    <MDBRow>
      <MDBCol md="12">
        <MDBCard cascade className="mdbcard">
          <MDBCardImage
            cascade
            className="img-fluid"
            overlay="white-light"
            hover
            src={img3}
          />

          <MDBCardBody cascade>
            <MDBCardTitle>Services</MDBCardTitle>
            <hr />
            <MDBCardText>
              <div className="mdbcard__text">
                <ul className="mdbcard__ul">
                  <li className="pr-2 black-text">Infra Management</li>
                  <li className="pr-2 black-text">Server Upgrades</li>
                  <li className="pr-2 black-text">
                    Disaster recovery and High availability
                  </li>
                  <li className="pr-2 black-text">Cloud transformation </li>
                  <li className="pr-2 black-text">Virtualisation</li>
                  <li className="pr-2 black-text">Digital Transformation</li>
                  <li className="pr-2 black-text">
                    Database Services:
                    <ul class="services__subdiv">
                      <li className="pr-2 black-text">
                        Oracle and SQL server installations
                      </li>
                      <li className="pr-2 black-text">Upgrades and tuning.</li>
                      <li className="pr-2 black-text">
                        HA and Disaster recovery using Oracle Golden Gate, Data
                        Guard, RMAN, RAC
                      </li>
                    </ul>
                  </li>
                  <li className="pr-2 black-text">
                    Human Resource Augmentation
                  </li>
                </ul>
              </div>
            </MDBCardText>
          </MDBCardBody>
        </MDBCard>
      </MDBCol>
    </MDBRow>
  );
};

export default CardMD3;
